import { styled } from "@chakra-ui/react";

export const StyledDiv = styled("div", {
  baseStyle: {
    boxSizing: "border-box",
    padding: "20px",
    borderRadius: "10px",
    display: "flex",
    minHeight: "100vh",
    width: "100%",
    overflow: "auto",
    flexDirection: "column",
  },
});